
import React, { Component } from 'react';

export default class FooterDescription extends Component {
  render() {
    return <div>
            <div className="row">
              <div className="small-12 columns primary callout">              
                <p>
                  This is a demo app to showcase real time communication using Firebase plus React through <a href="https://github.com/firebase/reactfire" target="_blank"> ReactFire </a>.
                  You case take a look at more details about it on its <a href="https://github.com/ViniciusTavares/LiveListWithReactAndFirebase"  target="_blank"> Github page </a>!
                </p>
              </div>
            </div>
        </div>
  }
}



